/* eslint-disable */

import './App.scss';
import { Link, Route, Switch } from "react-router-dom";
import { useEffect, useState } from 'react';
import { Formik } from "formik";

function SignUp() {

    const initialValues = {//       
        //     Formik       
        name: "",
        email: "",
        pw: "",
        repw: ""
    };

    //       .               
    const validate = (values) => {
        //        
        let errors = {};

        const nameRegex = /^[-]{2,6}$/;
        const emailRegex = /^[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*@[0-9a-zA-Z]([-_.]?[0-9a-zA-Z])*.[a-zA-Z]{2,3}$/i;
        const pwRegex = /^(?=.*\d)(?=.*[a-zA-Z])[0-9a-zA-Z]{8,16}$/;

        //   
        if (!values.name) {
            errors.name = " ";
            //  
        } else if (!nameRegex.test(values.name)) {
            errors.name = "   .";
        }

        //   
        if (!values.email) {
            errors.email = " ";
            //  
        } else if (!emailRegex.test(values.email)) {
            errors.email = "    ";
        }

        //   
        if (!values.pw) {
            errors.pw = " ";
            //  4  
        } else if (!pwRegex.test(values.pw)) {
            errors.pw = "   ";
        }

        //   
        if (!values.repw) {
            errors.repw = " ";
            //  4  
        } else if (values.repw != values.pw) {
            errors.repw = "  ";
        }

        return errors;
    };

    // submitForm :    
    const submitForm = (values) => {
        console.log(values);
    };

    return (

        // initialValues :       
        // validate :       
        // onSubmit :       
        //         .
        <Formik initialValues={initialValues} validate={validate} onSubmit={submitForm}>
            {(formik) => { // formik props
                // formik render API  .
                const { values, handleChange, handleSubmit, errors, touched, handleBlur, isValid, dirty } = formik;
                return (
                    <div className="signup">
                        <div className="background">
                            <div className="frame">
                                <div className="logIn">
                                    <div className="title">
                                        <span className="mento">MENTO</span>
                                        <span className="find">FIND YOUR MENTOR</span>
                                    </div>
                                    <div className="btn">
                                        <Link to="/signin"><button></button></Link>
                                    </div>
                                </div>
                                <div className="signUp">
                                    <div className="title"></div>
                                    {/* form */}
                                    <form onSubmit={handleSubmit} action="#" className="signUp">
                                        <div className="radio">
                                            <input type="radio" name="kind" id="radio1" required checked /><label for="radio1"> </label>
                                            <input type="radio" name="kind" id="radio2" required /><label for="radio2"> </label>
                                        </div>
                                        <div className="text">
                                            <label htmlFor="name">()</label>
                                            <input type="text" name="name" id="name" value={values.name} onChange={handleChange} onBlur={handleBlur}
                                                className={errors.name && touched.name ? "input-error" : null} placeholder="() " minlength="2" maxlength="6" />
                                            {/*      span  */}
                                            {errors.name && touched.name && (<span className="error">{errors.name}</span>)}

                                        </div>
                                        <div className="text">
                                            <label htmlFor="email">(Email)</label>
                                            <input type="email" name="email" id="email" value={values.email} onChange={handleChange} onBlur={handleBlur}
                                                className={errors.email && touched.email ? "input-error" : null} placeholder="(example@mento.com) " />
                                            {/*      span  */}
                                            {errors.email && touched.email && (<span className="error">{errors.email}</span>)}

                                        </div>
                                        <div className="text">
                                            <label htmlFor="pw"></label>
                                            <input type="password" name="pw" id="pw" value={values.pw} onChange={handleChange} onBlur={handleBlur}
                                                className={errors.pw && touched.pw ? "input-error" : null} placeholder="(+  8~16) " minlength="8" maxlength="16" />
                                            {/*      span  */}
                                            {errors.pw && touched.pw && (<span className="error">{errors.pw}</span>)}

                                        </div>
                                        <div className="text">
                                            <label htmlFor="repw"> </label>
                                            <input type="password" name="repw" id="repw" value={values.repw} onChange={handleChange} onBlur={handleBlur}
                                                className={errors.repw && touched.repw ? "input-error" : null} placeholder="(+  8~16) " minlength="8" maxlength="16" />
                                            {/*      span  */}
                                            {errors.repw && touched.repw && (<span className="error">{errors.repw}</span>)}

                                        </div>
                                        <div className="checkbox">
                                            <div className="cb">
                                                <input type="checkbox" name="cb1" id="cb1" required /><label for="cb1">,      ()</label>
                                            </div>
                                            <div className="cb">
                                                <input type="checkbox" name="cb2" id="cb2" required /><label for="cb2"> 14  ()</label>
                                            </div>
                                        </div>
                                        <div className="btn">
                                            <input type="submit" className={dirty && isValid ? "" : "disabled-btn"} value="" />
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                );
            }}
        </Formik>
    );
};
// }

export default SignUp;